import { Component,OnInit } from '@angular/core';
import { LoginService } from '../service/loginService';
import { LoginBean } from '../typeBean/loginBean';

@Component({
  selector: 'Login-Page',
  templateUrl: './app.login.login.html',
  styleUrls: ['../app.component.css'],
  providers: [LoginService]
})
export class LoginLoginComponent implements OnInit{
	constructor(private loginService: LoginService) { }
	
	responseStr:any = {};
	username = "";
	password = "";
	hasError = false;
	loginBean :LoginBean = new LoginBean();
	
	private handleError(error: any): Promise<any> {
	  console.error('An error occurred', error); // for demo purposes only
	  return Promise.reject(error.message || error);
	}
	
	login(): void {
	  this.loginService.loginUserPost(this.loginBean.username,this.loginBean.password).then(responseStr => {this.responseStr = responseStr;window.location.href="loggedin"})
			.catch(err=>{this.handleError(err);this.hasError = true;});
	}
	
	hideError():void{
		this.hasError = false;
	}
	
	ngOnInit(): void {
		var that = this;
		/*setInterval(function(){
			that.getRasPiHardware();
		},1000);
		*/
		//this.login();
	}
	  
}



