import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule }    from '@angular/common/http';
import { AppComponent } from './app.component';
import { LoginLoginComponent } from './login/app.login.login.component';
import { LogedInComponent } from './loggedin/app.logedin.component';
import { LoginForgetComponent } from './login/app.login.forget.component';
import { HardwareDetailsComponent } from './hardwareDetails/app.servercomhw.hardwaredetails.component';
import { HardwareSettingsComponent } from './hardwareSettings/app.servercomhw.hardwareSettings.component';
import { RouterModule }   from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

@NgModule({
  declarations: [
    AppComponent,LoginLoginComponent,HardwareDetailsComponent,HardwareSettingsComponent,LoginForgetComponent,LogedInComponent
  ],
  imports: [
  FormsModule,
    BrowserModule,
	HttpClientModule,
	RouterModule.forRoot([
		  {
			path: '',
			component: LoginLoginComponent
		  },
		  {
			path: 'loggedin',
			component: LogedInComponent
		  },
		  {
			path: 'forget',
			component: LoginForgetComponent
		  },
		  {
			path: 'details/:id',
			component: HardwareDetailsComponent
		  },
		  {
			path: 'settings/:id',
			component: HardwareSettingsComponent
		  }
		])
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule {}



